/* &#34-&#34,          */
.ymap-container
  position: relative
  overflow: hidden
  cursor: pointer
  background-image: url("../images/map/map-back.jpg")
  @supports (background-image: url('../images/map/map-back.webp'))
    background-image: url('../images/map/map-back.webp')
  background-position: center
  background-repeat: no-repeat
  background-size: cover
  width: 100%
  height: 100%
  max-width: 930px
  max-height: 700px

/* ,    . */
#map-yandex
  width: 100%
  height: 100%
  padding: 0
  margin: 0
  min-height: 850px
  position: relative
  z-index: 7
  cursor: pointer
  background-color: transparent
  @media #{$notebook}
    min-height: 700px
  @media #{$tablet}
    min-height: 870px


/*  ,      */
.loader
  position: absolute
  z-index: 15
  top: -100%
  left: 0
  box-sizing: border-box
  width: 100%
  height: 100%
  overflow: hidden
  color: #000000
  transition: opacity .4s ease
  opacity: 0
  background-color: rgba(0,0,0,.55)


.loader:after,
.loader:before
  box-sizing: border-box


.loader.is-active
  top: 0
  opacity: 1


.loader-default:after
  position: absolute
  top: calc(50% - 24px)
  left: calc(50% - 24px)
  width: 48px
  height: 48px
  content: ''
  animation: rotation 1s linear infinite
  border: solid 8px #ffffff
  border-left-color: transparent
  border-radius: 50%

@keyframes rotation
  from
    transform: rotate(0)

  to
    transform: rotate(359deg)

@keyframes blink
  from
    opacity: .5

  to
    opacity: 1


