.equipment
  position: relative
  background-image: url("../images/equipment/background.svg")
  background-repeat: no-repeat
  background-position: center
  background-size: contain
  @include section-indentP
  max-width: 1920px
  margin: 0 auto
  @media #{$tablet}
    background-image: none
  &:before
    position: absolute
    content: ''
    inset: 0
    z-index: -1
    background: #F0FFFF

  &__inner
    position: relative
    display: flex

  &__tabs-module
    display: none
    @media #{$tablet}
      display: block
    &.tabs-pane-show
      display: block
  &__img
    max-width: fit-content

  &__tabs-inner
    display: flex
    flex-direction: column
    position: absolute
    right: 0
    top: calc(50% + 10px)
    min-width: 120px
    min-height: 150px
    @media #{$tablet}
      display: none

  &__info-inner
    max-width: 530px

  &__title-color
    color: #099

  &__name
    @include color-text(#ADADAD)
    @media #{$mobile}
      font-size: 12px

  &__title
    margin-bottom: 12px
    @media #{$mobile}
      font-size: 24px
      line-height: 1.5
    @media #{$mobile-small}
      font-size: 20px
      line-height: 1.2

  &__text
    margin-bottom: 24px
    @include color-text(#5F5F5F)

  &__button-inner
    display: flex
    align-items: center
    @media #{$tablet}
      align-items: flex-start
      flex-direction: column
      gap: 18px
      margin-bottom: 32px


  &__button
    @include button
    @include button-contour-color
    margin-right: 26px
    @media #{$mobile}
      margin-right: 0

  &__all-photo
    display: flex
    align-items: center
    font-weight: 600
    @media #{$mobile}
      font-size: 14px
    &:before
      content: ''
      display: block
      min-width: 24px
      height: 24px
